	module.exports = {"data":{"markdownRemark":{"html":"<h2>Installing</h2>\n<p>You can install <code>html2canvas</code> through npm or <a href=\"https://github.com/niklasvh/html2canvas/releases\">download a built release</a>.</p>\n<h3>npm</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>npm install html2canvas</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">import</span> html2canvas <span class=\"token keyword\">from</span> <span class=\"token string\">'html2canvas'</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h2>Usage</h2>\n<p>To render an <code>element</code> with html2canvas with some (optional) <a href=\"/configuration/\">options</a>, simply call <code>html2canvas(element, options);</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token function\">html2canvas</span><span class=\"token punctuation\">(</span>document<span class=\"token punctuation\">.</span>body<span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">then</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span>canvas<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    document<span class=\"token punctuation\">.</span>body<span class=\"token punctuation\">.</span><span class=\"token function\">appendChild</span><span class=\"token punctuation\">(</span>canvas<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>","frontmatter":{"title":"Getting Started","description":"Learn how to start using html2canvas","previousUrl":"/documentation","previousTitle":"About","nextUrl":"/configuration","nextTitle":"Configuration"}}},"pathContext":{"slug":"/getting-started/"}}

